@import url("https://fonts.googleapis.com/css2?family=Allura&display=swap");

.App {
  text-align: center;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* background-color: #f0eeed; */
  background-color: black;
  color: #d0c7bd;
}
.App-header {
  display: flex;
  flex-flow: column;
  align-items: center;
  width: 100%;
  font-size: calc(10px + 1vmin);
  padding-top: 30px;
}

.App-header-logo {
  font-size: 100px;
}

.App-header-logo > p {
  margin: 0;
  font-family: "Allura", cursive;
}

.App-header-logo > p:first-child {
  font-size: 60px;
  margin: 0;
  font-family: "Allura", cursive;
}

.App-header-links {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  list-style-type: none;
  font-size: 16px;
  padding: 10px 0;
  border-top: 0.5px solid #252525;
}

.App-header-socials {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20%;
}

.App-header-socials > a {
  font-size: 16px;
}

li {
  display: flex;
  align-items: center;
  padding: 0 40px;
}
a {
  text-decoration: none;
  color: #d0c7bd;
}

.Home {
  height: 100%;
  width: 100%;
  display: flex;
  flex-flow: wrap;
  justify-content: center;
}

.block {
  width: 100%;
  display: flex;
  align-items: flex-end;
  justify-content: space-around;
  padding-bottom: 50px;
}

